.flexbox {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}

input[type=text] {
    width: 300px;
    padding: 10px;
    font-size: 20px;
}

button {
    padding: 10px;
    width: 100px;
    font-size: 20px;
    margin-left: 10px;
}

.gifs {
    display: grid;
    grid-template-columns: repeat(auto-fit, 300px);
    grid-auto-flow: row;
    gap: 25px;
    margin: 20px 1%;
    .gifContainer {
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        padding: 18px;
        background: white;
    }
    img {
        display: block;
        max-width: 100%;
    }

}

.flexboxSearchGifs {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    padding-top: 20px;
}

.viewMemesButtonContainer {
    margin-right: 40px
}

.searchGifForm {
    margin-left: 40px
}

.createMemeForm {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    justify-content: center;
  
}

#createMemeSection {
    display:none;
    flex-direction:column;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
}

.createMemeSection { 
    width: 100%;
    height: 100%;
    background-color:rgba(1,1,1,0.9);
    position: fixed;
    display: flex;
    margin: 0;
    z-index: 999
}

.selectedGif {
    width: 400px;
}



.eachMemeStyle {
   display: grid;
    grid-template-columns: repeat(auto-fit, 300px);
    grid-auto-flow: row;
    gap: 25px;
    margin: 20px 1%;
    


    .eachMemeStyleContainer{
        padding: 18px;
        background: white;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
    }
        

        img{
            display: block;
            max-width: 100%;
        }

       p{
            font-size: 18px;
            font-weight: bold;
            text-transform: uppercase;
            text-align: center;
            font-family: $heading;
            color: white;
            letter-spacing: 1.5px;
            margin-bottom: 10px;
            margin-top: 10px;

       } 

}

.creatingMemeText {
    font-size: 20px;
    text-align: center;
    margin-bottom: 10px;
}


.voteBtns{
    display: flex;
    justify-content: center;
    button{
        border-radius: 10px;
    }
    p{
        color: black;
    }
}



.noGifFoundText {
    text-align: center;
    font-size: 25px;
}

@media (max-width: 736px) {
    .flexboxSearchGifs {
        justify-content: center;
        margin: 0;
        .viewMemesButtonContainer, 
        .searchGifForm {
            margin: 0;
        }
    }
}

.gifContainer { 
    border: none;
    outline: none;
    cursor: pointer;
    position: relative;
    z-index: 0;
    border-radius: 10px;
}

.gifContainer:before {
    content: '';
    background: linear-gradient(45deg, #ff0000, #ff7300, #fffb00, #48ff00, #00ffd5, #002bff, #7a00ff, #ff00c8, #ff0000);
    position: absolute;
    top: -2px;
    left:-2px;
    background-size: 400%;
    z-index: -1;
    filter: blur(5px);
    width: calc(100% + 4px);
    height: calc(100% + 4px);
    animation: glowing 20s linear infinite;
    opacity: 0;
    transition: opacity .3s ease-in-out;
    border-radius: 10px;
}

.gifContainer:active {
    color: #000
}


.gifContainer:hover:before {
    opacity: 1;
}

.gifContainer:after {
    z-index: -1;
    content: '';
    position: absolute;
    width: 100%;
    height: 100%;
    background: #111;
    left: 0;
    top: 0;
    border-radius: 10px;
}